<template>
    <div id="index">
        <div class="cityTop">
            <h4 v-model="city">城市：{{city}}</h4>
            <span @click="Check">+</span>
        </div>
        <div class="weather">
            <div class="today">
                <div class="today-content">
                    <div class="today-item1">
                        {{today.temp}}°
                    </div>
                    <div class="today-item1">
                        <span class="today-img">
                             <img :src="today.img" alt="">
                        </span>
                        <span>{{today.weather}}</span>
                        <span>{{today.templow}}°~{{today.temphigh}}°</span>
                    </div>
                </div>
            </div>
            <div class="week">
                <div class="week-title">
                    <h4 class="week-title-item1">预报</h4>
                    <h4 class="week-title-item2">
                        <span>今天</span>
                        <span>{{today.week}}</span>
                    </h4>
                </div>
                <ul class="today-time">
                    <li v-for="v in (hourly.slice(0,12))">
                        <span>{{v.time}}</span>
                        <span><img :src="v.img" alt=""></span>
                        <span>{{v.temp}}°</span>
                    </li>
                </ul>
                <ul class="week-time">
                    <li v-for="v in (week.slice(0,6))">
                        <h4 style="color: #fff">{{v.week}}</h4>
                        <div class="week-time-bottom">
                            <div><img :src="v.day.img" alt=""></div>
                            <div>
                                <span style="color: #fff">{{v.day.temphigh}}°</span>
                                <span style="color: #fff">18°</span>
                            </div>
                        </div>
                        <h4 style="font-size: .3rem;color: #ff4211">{{v.day.weather}}</h4>
                    </li>
                </ul>
            </div>
        </div>
        <div class="indexs">
            <div class="index-titles">
                生活指数
            </div>
            <ul>
                <li v-for="v in index">
                    <div class="box">
                        <img :src="today.img" alt="">
                    </div>
                    <div class="content">
                        <h3 style="color: #fff">{{v.iname}}</h3>
                        <p>{{v.detail}}</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Jquery from 'jquery';
    export default {
        components:{
            Jquery

        },
        name: 'index',
        data: () => ({
            city:'',
            today:
                {
                    temphigh:'',
                    templow:'',
                    weather:'',
                    week:'',
                    winddirect:'',
                    windpower:'',
                    img:''

                },
            week:[],
            hourly:[],
            index:[]
        }),
        beforeMount(){
            if(JSON.stringify(this.$route.params) == "{}"){
                var that = this;
                Jquery.ajax({
                    url:'http://whois.pconline.com.cn/ipJson.jsp',
                    dataType:'jsonp',
                    async:false,
                    success:function (r) {
                        that.city =r.city;
                    }
                });
            }else{
                this.city=this.$route.params.areaSelected;
            }
        },
        methods:{
            Check(){
                this.$router.push({name:"check"})
            }
        },
        watch:{
            city:function (val,oval) {
                var that = this;
                Jquery.ajax({
                    url: 'http://api.jisuapi.com/weather/query?appkey=7bd1c82d3c3e9ecd&city=' + val,
                    dataType:'jsonp',
                    success:function (r) {
                        console.log(r);
                        var data = r.result;
                        that.today.temp = data.temp;
                        that.today.temphigh = data.temphigh;
                        that.today.templow = data.templow;
                        that.today.weather = data.weather;
                        that.today.week = data.week;
                        that.today.winddirect = data.winddirect;
                        that.today.windpower = data.windpower;
                        that.today.img = 'static/weathercn02/' + data.img + '.png';
                        for(var i = 0;i<data.daily.length;i++){
                            data.daily[i].day.img = 'static/weathercn02/' + data.daily[i].day.img + '.png';
                            that.week.push(data.daily[i])
                        }
                        for(var i=0;i<data.index.length;i++){
                            that.index.push(data.index[i])
                        }
                        for(var i=0;i<data.hourly.length;i++){
                            data.hourly[i].img = 'static/weathercn02/' + data.hourly[i].img + '.png';
                            that.hourly.push(data.hourly[i])
                        }
                    }
                })
            }
        }
    }
</script>
<style scoped>
    #index {
        width: 100vw;
        height: 98%;
    }
    .cityTop{
        padding: 0 .2rem;
        width: calc(100% - .4rem);
        height: 1rem;
        color: #fff;
        background: #5da5ea;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .cityTop span{
        font-size: .6rem;
    }
    .weather{
        width: 100%;
        height: auto;
    }
    .today{
        width: 100%;
        height: 2rem;
    }
    .today-content{
        width: 3rem;
        height: 2rem;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
    }
    .today-item1{
        height: 100%;
        display: inline-block;
    }
    .today-item1:nth-child(1){
        width: 1.6rem;
        border-right:1px solid #fff;
        font-size: 1.1rem;
        color: #fff;
        line-height: 2rem;
        text-align: center;
        padding-right: .1rem;
    }
    .today-item1:nth-child(2){
        width: 1.4rem;
    }
    .today-item1:nth-child(2) span{
        display: block;
        color: #fff;
        width: 1.2rem;
        margin:.1rem auto;
        line-height: .4rem;
        text-align: center;
    }
    .today-item1:nth-child(2) span:nth-child(1){
        width: .8rem;
        display: block;
        height: .6rem;
        margin-top: .1rem;
    }
    .week-title{
        padding: 0 .3rem;
        width: calc(100% - .6rem);
        height: .8rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color:#fff;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3) 0px 0px 4px;
        border-bottom: 1px dashed #e7e7e7;
    }
    .week-title .week-title-item2 span:nth-child(1){
        margin-right: .2rem;
    }
    .week-title .week-title-item2 span:nth-child(2){
        color: #fff;
    }
    .today-time{
        padding:0 .3rem;
        width: calc(100% - .6rem);
        height: 4rem;
    }
    .today-time li{
        list-style: none;
        width: 16.66%;
        height: 2rem;
        float: left;
        display: flex;
        flex-direction:column;
        align-items: center;
        margin-bottom: .2rem;
        border-bottom: 1px dashed #bbbbbb;
    }
    .today-time li span{
        display: block;
        height: .66rem;
        line-height: .6rem;
        text-align: center;
        color: #fff;
    }
    .today-time li span:nth-child(2){
        width: .8rem;
    }
    .week-time{
        padding:0 .2rem;
        width: calc(100% - .4rem);
        height: 3rem;
    }
    .week-time li{
        list-style: none;
        width: 16.66%;
        height: 2rem;
        float: left;
        display: flex;
        flex-direction:column;
        align-items: center;
    }
    .week-time li h4{
        line-height: 1rem;
        height: .6rem;
        color:rgba(10, 10, 10, 0.44) ;
        font-size: .2rem;
    }
    .week-time .week-time-bottom{
        margin-top: .2rem;
        width: 100% ;
        height:1.5rem;
        display: flex;
        justify-content:start;
        align-items: center;
    }
    .week-time .week-time-bottom div{
        width: 40%;
        height: 100%;
    }
    .week-time .week-time-bottom div:nth-child(1){
        height: .6rem;
        margin-left: .2rem;
    }
    .week-time .week-time-bottom div:nth-child(2){
        display: flex;
        flex-direction: column;
        align-content: center;
    }
    .week-time .week-time-bottom div:nth-child(2) span{
        display: block;
        width: 100%;
        line-height: .4rem;
        text-align: center;
        color:rgba(10, 10, 10, 0.44) ;
        font-size: .05rem;
    }
    .indexs{
        width:calc(100% - .4rem) ;
        padding: 0 .2rem;
        height: auto;
    }
    .indexs .index-titles{
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        color:#fff;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3) 0px 0px 4px;
        border-bottom: 1px dashed #e7e7e7;

    }
    .indexs ul{
        margin-top: .3rem;
    }
    .indexs ul li{
        width: 100%;
        height: 1.5rem;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: .2rem 0;
    }
    .indexs .box{
        width: 1rem;
        height: 1rem;
    }
    .indexs .content{
        width: 5.5rem;
        height: 1rem;
    }
    p{
        margin-top: .1rem;
        font-size: .2rem;
        color: #fff;
    }
</style>



