import Vue from 'vue'
import Router from 'vue-router'
import Vueresource from "vue-resource"
import Index from '@/components/index'
import Check from '@/components/check'

Vue.use(Router)
Vue.use(Vueresource);
Vue.http.options.emulateJSON=true;
export default new Router({
    routes: [
        {
            path: '/',
            name: 'index',
            component: Index
        },
        {
            path:'/check',
            name:'check',
            component:Check
        }
    ]
})



