<template>
    <div class="check">
       <div class="top">
           <div class="checkbox">
               <select name="province" id="province" @click="Getprovince" v-model="selected">
                   <option value="">--请选择省份--</option>
                   <option v-for="v in provinceList" :value="v.id">{{v.name}}</option>
               </select>
               <select name="city" id="city" v-model="citySelected">
                   <option value="">--请选择城市--</option>
                   <option v-for="v in cityList" :value="v.cityid">{{v.city}}</option>
               </select>
               <select name="area" id="area" v-model="areaSelected">
                   <option value=""> --请选择区--</option>
                   <option v-for="v in areaList">{{v.city}}</option>
               </select>
           </div>
       </div>
        <div class="bottom">
            <button @click="back">返回</button>
            <button @click="submit">确认</button>
        </div>
    </div>
</template>
<script>
    import Jquery from 'jquery';
    export default {
        name: "check",
        components:{
            Jquery
        },
        data:()=>({
            provinceList:[],
            cityList:[],
            areaList:[],
            selected:'',
            citySelected:'',
            areaSelected:'',
        }),
        methods:{
            Getprovince(){
                var that = this;
                Jquery.ajax({
                    url:'http://api.jisuapi.com/area/province?appkey=7bd1c82d3c3e9ecd',
                    dataType:'jsonp',
                    async:false,
                    success:function (r) {
                        var data = r.result
                        for(var i = 0;i<data.length;i++){
                            that.provinceList.push(data[i])
                        }
                    }
                })
            },
            back(){//返回
                this.$router.push({name:'index'})
            },
            submit(){//提交
                this.$router.push({name:'index',params:{areaSelected:this.areaSelected}});
            }
        },
        watch:{
            selected:function (val,oval) {
                var that = this;
                Jquery.ajax({
                    url:'http://api.jisuapi.com/weather/city?appkey=7bd1c82d3c3e9ecd',
                    dataType:'jsonp',
                    async:false,
                    success:function (r) {
                        var data = r.result;
                        for(var i = 0;i<data.length;i++){
                            if(data[i].parentid == val){
                                that.cityList.push(data[i])
                            }
                        }
                    }
                })
            },
            citySelected:function (val,oval) {
                var that = this;
                Jquery.ajax({
                    url:'http://api.jisuapi.com/weather/city?appkey=7bd1c82d3c3e9ecd',
                    dataType:'jsonp',
                    async:false,
                    success:function (r) {
                        var data = r.result;
                        for(var i = 0;i<data.length;i++){
                            if(data[i].parentid == val){
                                that.areaList.push(data[i])
                            }
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped>
.top{
    margin-top: .5rem;
    width: 100%;
    height: 6rem;
}
   select{
       width: 32%;
       height: .6rem;
   }
    .bottom{
        width: 100%;
        height: 3rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
.bottom button{
    width: 1.5rem;
    height: .6rem;
    text-align: center;
    margin: 0 .3rem;
}
</style>


