<template>
    <div id="app">
        <div class="banner">
            <swiper :options="swiperOption" style="height: 100%;width: 100%;">
                <!--<swiper-slide v-for="slide in swiperSlides" :key="slide.id">
                    <img :src="slide" alt="">
                </swiper-slide>-->
                <swiper-slide><img src="./assets/img/7.jpg" alt=""></swiper-slide>
                <swiper-slide><img src="./assets/img/8.jpg" alt=""></swiper-slide>
                <swiper-slide><img src="./assets/img/9.jpg" alt=""></swiper-slide>
                <swiper-slide><img src="./assets/img/10.jpg" alt=""></swiper-slide>
                <swiper-slide><img src="./assets/img/11.jpg" alt=""></swiper-slide>
                <swiper-slide><img src="./assets/img/12.jpg" alt=""></swiper-slide>
            </swiper>
        </div>
        <router-view/>
    </div>
</template>
<script>
    import {swiper, swiperSlide} from 'vue-awesome-swiper';
    export default {
        name: 'App',
        components: {
            swiper,
            swiperSlide
        },
        data: () => ({
            swiperOption: {//swiper官网看api，链接http://www.swiper.com.cn/api/
                notNextTick: true,
                autoplay: true,
                speed: 300,//滑动速度
                direction: 'horizontal',//Slides的滑动方向(vertical垂直)（horizontal水平）
                grabCursor: true,
                setWrapperSize: false,
                autoHeight: false,
                pagination: '.swiper-pagination',
                paginationClickable: true,
                prevButton: '.swiper-button-prev',//上一张
                nextButton: '.swiper-button-next',//下一张
                scrollbar: '.swiper-scrollbar',//滚动条
                mousewheelControl: true,
                observeParents: true,
                loop: true,
                centeredSlides: true,
                keyboard: true,
                // 如果自行设计了插件，那么插件的一些配置相关参数，也应该出现在这个对象中，如下debugger
                debugger: true,
            },
            // swiperSlides: ['./assets/img/1.jpg', './assets/img/2.jpg', './assets/img/3.jpg', './assets/img/4.jpg', './assets/img/6.jpg'],
        }),
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        width: 100vw;
        height: 100vh;
        background-image: url("./assets/img/timg.jpg");
        font-size: 14px;
    }

    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
    }

    .banner {
        width: 100%;
        height: 4rem;
    }

    img {
        width: 100%;
        height: 100%;
        display: block;
    }
</style>



